import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../../Theme/themes";
import { BrandType } from "../../../types/brand";
import Info from "../Info";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type RoomInfoStoryProps = {
    mode: string,
    brand: BrandType,
    bedInfo: string,
    noRefundable: boolean
}

const RoomInfoStory = ({
    mode, brand, bedInfo, noRefundable
}: RoomInfoStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Room/Info">
                <Typography variant="h3">RoomInfo</Typography>
                <br />
                <Divider />
                <br />
                <Grid item xs={2}>
                    <Info bedInfo={bedInfo} noRefundable={noRefundable}/>
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
        bedInfo: "Uma cama de casal ou 2 de solteiro",
        noRefundable: true
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        bedInfo: { type: "string" },
        noRefundable: { type: "boolean" }
    }
}

storiesOf("Components/Room/Info", module)
    .add("Default", (args) => <RoomInfoStory {...args}/>, argsControls);
